<template>
  <div class="header">
    <div class="header-left">
      <div class="iconfont back-icon">&#xe624;</div>
    </div>
    <div class="header-input">
      <span class="iconfont">&#xe632;</span>
      输入城市/景点/游玩主题
    </div>
    <router-link to="/city">
      <div class="header-right">
        {{this.city}}
        <span class="iconfont arrow-icon">&#xe64a;</span>
      </div>
    </router-link>
  </div>
</template>

<script>
  import { mapState } from 'vuex'
export default{
  name: 'HomeHeader',
  computed: {
    ...mapState(['city'])
  }
}
</script>

<style lang="stylus" scoped>
  /* 引用变量设置样式 必须以@import开头、路径前边必须加~、styles是在build文件夹下的webpack.base.conf.js中设置的简写路径 详见34行 */
  @import '~styles/varibles.styl'
  @import '~styles/mixins.styl'
  .header
    display:flex
    line-height:$headerHeight
    background-color:$bgColor
    color:#fff
    .header-left
      width:.64rem
      float:left
      .back-icon
        text-align:center
        font-size:.4rem
    .header-input
      flex:1
      height:.62rem
      line-height:.62rem
      margin-top:.12rem
      margin-left:.2rem
      padding-left:.2rem
      background-color:#fff
      border-radius:.1rem
      color:#ccc
    .header-right
      float:right
      min-width:1.04rem
      padding:0 .1rem
      text-align:center
      color:#fff
      ellipsis()
      .arrow-icon
        margin-left:-.04rem
        font-size:.24rem
</style>



